@import url('Global.css');

@font-face {
    font-family: mns_chinese;
    src: url('../fonts/mns_chinese.otf');
}

body, html {
    background-color: var(--blue-white);
}

#cv-container {
    width: 100%;
    max-width: 37.5rem;
    overflow-x: hidden;
    margin: 0 auto;
    height: 100%;
    padding: 0.5rem;
    box-sizing: border-box;
    background-color: var(--blue-white);   
    font-family: mns_chinese;
    position: relative;
}

#self-information-container {
    width: 100%;
    /* height: 50%; */
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#basic-information-container {
    margin: 5rem 0 0 0rem;
    z-index: 1;
    position: relative;
}

#my-one-inch-photo {
    width: 8rem;
    padding: 0rem 0.5rem 0rem 0rem;
    height: 8rem;
    overflow: hidden;
    position: absolute;
    left: 0rem;
    top: -4rem;
    border-radius: 8rem;
    z-index: -1;
}

#my-one-inch-photo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center center;
}

#self-information-list {
    list-style: none;
    line-height: 2.3rem;
}
.self-information-item .self-information-break {
    color: rgb(200, 200, 200);
    display: inline;
}
#name-item {
    font-size: 4.5rem;
    padding: 1.5rem 0;
    margin:1rem 0 0 4.6rem;
}

.information-title {
    font-size: 1.1rem;
    font-weight: bold;
}
.information-container {
    padding-top: 0.5rem;
}

.information-description {
    line-height: 2.3rem;
}
#pics-description {
    width: 100%;
}
#pics-list .pics-item img {
    width: 100%;
    object-fit: contain;
    object-position: center center;
}
