#secret {
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 250, 1);
    font-family: "Arial","Microsoft YaHei","黑体","宋体",sans-serif;
    padding: 0.5rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#secret-content {
    overflow: scroll;
    line-height: 2rem;
    /* position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
}

#main-secret-content li {
    list-style: none;
    min-height: 1rem;
    font-size: 1rem;
}

.time {
    font-size: 0.7rem;
    color:#375d8f;
}